
.rankHead{
  width: 100%;
  line-height: 3rem;
  background: -webkit-linear-gradient(top,rgba(0,0,0,.6),transparent);
  padding: 0 2.1429rem;
  position: relative;
  text-align: center;
  color: #fff;
  box-sizing: border-box;
}
.op{
    background: #2ca2f9 !important;
}
.white{
    box-sizing: border-box !important;
    background: #fff !important;
    color: #333 !important;
    box-shadow: 0 .1785rem .1785rem 0 #f4f4f4 !important;
}
.back{
    width: 2.1429rem;
    height: 2.8031rem;
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    cursor: pointer;
}
.back i{
    width: 0.85714rem;
    height: 1.42857rem;
    margin-top: .687265rem;
    display: inline-block;
    background: url("http://m.kugou.com/v3/static/images/index/goback.png") no-repeat;
    background-size: 100%;
}



