
.player{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4.2143rem;
  background: rgba(0,0,0,.9);
  color: #fff;
}
.player p{
  margin: 0;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
}
.detail{
  width: 8rem;
  margin-top: -3.5rem;
  display: inline-block;
  vertical-align: middle;
}
.detail p:nth-child(1){
  width: 100%;
  line-height: 1.7rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: .8571rem;
  color: #fff;
}
.detail p:nth-child(2){
  line-height: 1.3;
  font-size: .7143rem;
  color: #888;
}
.player .img{
  height: 1.786rem;
  display: inline-block;
}
.player .img img{
  height: 1.786rem;
  width: 1.786rem;
  vertical-align: middle;
  margin-top: -3rem;
  margin-right: 0.5rem;
}
.overlay{
  width: 100%;
  position: absolute;
  top: 3.2143rem;
  bottom: 0;
  z-index: 3;
  background: #fff;
}
.bg-img{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background:no-repeat 50%;
  background-size: cover;
  filter: blur(15px);
  -webkit-filter:blur(15px);
}
.bg-mask{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,.6);
}
.play{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.rankHead{
  width: 100%;
  line-height: 3rem;
  background: -webkit-linear-gradient(top,rgba(0,0,0,.6),transparent);
  padding: 0 2.1429rem;
  position: absolute;
  text-align: center;
  color: #fff;
  box-sizing: border-box;
  z-index: 1;
}
.back{
  width: 2.1429rem;
  height: 2.8031rem;
  position: absolute;
  top: 0;
  left: 0;
  text-align: center;
  cursor: pointer;
}
.play .back i{
  width: 0.85714rem;
  height: 1.42857rem;
  margin-top: .687265rem;
  display: inline-block;
  background: url("http://m.kugou.com/v3/static/images/index/goback_1.png") no-repeat;
  background-size: 100%;
}
.play-panel{
  width: 100%;
  padding-top: 3rem;
  position: relative;
}
.img-box{
  width: 11rem;
  height: 11rem;
  margin: 2rem auto 0;
}
.img-box img{
  width: 100%;
}
.lrc-box{
  height: 5.4rem;
  overflow: hidden;
  margin-top: 1rem;
  text-align: center;
  color: #afabac;
}
.lrc-box p{
  margin: 0;
  width: 100%;
  height: 1.7rem;
  line-height: 1.7rem;
  overflow: hidden;
  font-size: 0.85rem;
  vertical-align: top;
  white-space: nowrap;
}
.handle{
  color: #d1c90e;
}
.time-wrap{
  width: 100%;
  padding: .9rem .7rem;
  display: box;
  display: -webkit-box;
  box-align:center;
  -webkit-box-align:center;
  font-size: .6rem;
  color: #dcdcdc;
}
.operate{
  font-size: 0;
  text-align: center;
}
.operate .prev{
  background: url("../assets/play_prev.png") no-repeat;
  background-size: 100%;
  width: 2.4rem;
  height: 2.4rem;
  margin: .9rem;
  display: inline-block;
  vertical-align: middle;
}
.operate .isplay{
  background: url("../assets/play_pause.png") no-repeat;
  background-size: 100%;
  width: 3.2rem;
  height: 3.2rem;
  margin: .9rem;
  display: inline-block;
  vertical-align: middle;
}
.stop{
  background-image: url("../assets/play_play.png") !important;
}
.operate .next{
  background: url("../assets/play_next.png") no-repeat;
  background-size: 100%;
  width: 2.4rem;
  height: 2.4rem;
  margin: .9rem;
  display: inline-block;
  vertical-align: middle;
}



