
.header-nav{
    width: 100%;
    height: 3.2143rem;
    line-height: 3rem;
    box-shadow: 0 .1785rem .1785rem 0 #f4f4f4;
    background: #fff;
}
.header-nav ul{
    margin: 0;
    width: 100%;
    padding: 0 .7143rem;
    display: box;
    display: -webkit-box;
    box-sizing: border-box;
}
.header-nav li{
  width: 20%;
  text-align: center;
  list-style: none;
  box-flex:1.0;
  -webkit-box-flex: 1.0;
}
.header-nav a{
  width: 100%;
  color: #333;
  font-size: 1.1429rem;
  text-decoration: none;
  display: block;
  box-sizing: border-box;
}
.cur a{
  border-bottom: .2143rem solid #33a3f5;
}



