
.mycontent{
  padding-top: 6.4286rem;
  width: 100%;
}
.swiper-container{
  margin-top: .3571rem;
}
.swiper-slide img{
  max-width: 100%;
  height: 8.5714rem;
  margin: 0 auto;
  display: block;
}
.swiper-pagination-bullet{
  width: .5714rem;
  height: .5714rem;
}
.swiper-pagination-bullet-active{
  background: #fff;
}
.mycontent .list ul{
  padding-left: .7143rem;
  margin: 0;
}
.mycontent .list li{
  width: 100%;
  height: 4.0714rem;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
  display: table;
}
.mycontent .left{
  width: 18.2rem;
  padding-right: .3571rem;
  display: table-cell;
  vertical-align: middle;
  padding-left: .3571rem;
  cursor: pointer;
  font-size: 1rem;
  box-sizing: border-box;
}
.mycontent .right{
  width: 2.5rem;
  height: 100%;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}
.mycontent .right i{
  display: inline-block;
  width: 1.0357rem;
  height: 1.0357rem;
  background: url("../assets/download_icon.png");
  background-size: 100%;
  margin: 0 auto;
}



