
ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
p{
  margin: 0;
  padding: 0;
}
.singerDtail{
  position: absolute;
  top: 6.2143rem;
  background: #fff;
  width: 100%;
}
.singerDtail>ul{
  color: #333;
  background: #eee;
}
.singerDtail>ul>li{
  line-height: 1.5rem;
  font-size: 0.9rem;
}
.type li{
  padding:0 .7143rem;
  line-height: 3rem;
  background: #fff;
  font-size: 1rem;
}
.singerDtail>ul>li>div{
  margin-left:  .6429rem;
}
.type div{
  padding:.6429rem 0;
  border-bottom: 1px solid #ccc;
  position: relative;
}
.type li:last-child div{
  border: none;
}
.type img{
  height: 3rem;
  position: absolute;
  top: .6429rem;
  left: 0;
}
.type p{
  padding-left: 4rem;
}
#left ul{
  list-style:none;
  position:fixed;
  right: 0;
  top:6.2143rem;
  z-index:0;
}
#left li{
  height:1.1rem;
  line-height: 1.1rem;
  font-size: 0.8rem;
  color: #222;
  width: 25px;
  text-align: center;
}
#left div{
  font-size:6vh;
  position:fixed;
  top:47vh;
  text-align:center;
  width:100%;
}
#left div a{
  display: block;
  width: 4rem;
  height: 4rem;
  line-height: 4rem;
  background: rgba(0,0,0,0.4);
  margin: 0 auto;
  color: #fff;
}



