
.progress{
    height: .2rem;
    margin: 0 .8rem;
    background: #6c6b70;
    width: 15rem;
}
.progressing{
    position: absolute;
    width: .7rem;
    height: .7rem;
    background: #3195fd;
    border-radius: 50%;
    top: -0.25rem;
    right: -0.35rem;
}
.progressed{
    position: relative;
    background: #3195fd;
    height: 100%;
    width: 0;
}



