
.content{
  padding-top: 3.2143rem;
  width: 100%;
}
.banner{
  width: 100%;
  height: 12.5rem;
  position: relative;
  overflow: hidden;
}
.banner img{
  margin-top: -25px;
  width: 100%;
}
.list ul{
  padding-left: .7143rem;
  margin: 0;
}
.list li{
  width: 100%;
  height: 4.0714rem;
  border-bottom: 1px solid #e5e5e5;
  position: relative;
  display: table;
}
.left{
  width: 16.5rem;
  padding-right: .3571rem;
  display: table-cell;
  vertical-align: middle;
  padding-left: 2.0674rem;
  cursor: pointer;
  font-size: 1rem;
  box-sizing: border-box;
}
.content .list span{
  padding: 0 .5rem;
  height: 1.0714rem;
  line-height: 1.0714rem;
  border-radius: 0.5rem;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -.5357rem;
  text-align: center;
  font-size: 0.7143rem;
  display: inline-block;
  color: #999;
}
.list li:nth-child(1)>span{
  background: #e80000;
  color: #fff;
}
.list li:nth-child(2)>span{
  background: #ff7200;
  color: #fff;
}
.list li:nth-child(3)>span{
  background: #f8b300;
  color: #fff;
}
.right{
  width: 2.5rem;
  height: 100%;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  display: table-cell;
}
.right i{
  display: inline-block;
  width: 1.0357rem;
  height: 1.0357rem;
  background: url("../assets/download_icon.png");
  background-size: 100%;
  margin: 0 auto;
}
.player{
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 4.2143rem;
  background: rgba(0,0,0,.9);
  color: #fff;
}
.player p{
  margin: 0;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
}
.detail{
  width: 8rem;
  margin-top: -3.5rem;
  display: inline-block;
  vertical-align: middle;
}
.detail p:nth-child(1){
  width: 100%;
  line-height: 1.7rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: .8571rem;
  color: #fff;
}
.detail p:nth-child(2){
  line-height: 1.3;
  font-size: .7143rem;
  color: #888;
}
.player .img{
  height: 1.786rem;
  display: inline-block;
}
.player .img img{
  height: 1.786rem;
  width: 1.786rem;
  vertical-align: middle;
  margin-top: -3rem;
  margin-right: 0.5rem;
}



