
.singer{
  padding-top: 6.4286rem;
  width: 100%;
}
.singer ul{
  padding: 0!important;
  margin: 1rem .7143rem 1.0714rem;
  background: #fbfbfb;
  border:1px solid #e4e4e4;
  border-radius: 5px;
}
.singer li{
  list-style: none;
  padding:.7143rem;
  font-size: 1rem;
  color: #333;
  border-top:1px solid #e4e4e4;
}
.singer li:nth-child(1){
  border: none;
}
.singer i{
  width: .57143rem;
  height: 1rem;
  margin-top: 0.2rem;
  background: url("../assets/arrow_icon.png") no-repeat;
  background-size: 100%;
  display: block;
  float: right;
}



